* {
  font-family: "Roboto", sans-serif;
}

.sanPhamDoc {
  width: 185px;
  padding: 10px;
  background-color: white;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  position: relative;
  .hinhAnh {
    width: 100%;
    height: 165px;
    // border: 1px solid silver;
    display: flex;
    align-items: center;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .tenSp {
    width: 100%;
    height: 53px;
    overflow: hidden;
    margin-top: 5px;
    p {
      font-size: 14px;
      font-weight: 400;
      // text-transform: capitalize;
      line-height: normal;
      color: rgb(90, 90, 90);
    }
  }
  .giaSp {
    display: flex;
    justify-content: space-between;
    align-items: end;
    margin-top: 5px;
    p.giaGiam {
      font-size: 16px;
      font-weight: 500;
      color: red;
    }
    p.giaBan {
      font-size: 13px;
      font-weight: 500;
      text-decoration: line-through;
      color: silver;
    }
  }
  .phanTram {
    position: absolute;
    top: 0;
    left: 0;
    // width: 50px;
    padding: 0 5px;
    background-color: red;
    border-radius: 10px 0 10px 0;
    color: white;

    background: linear-gradient(
      90deg,
      #ff0000,
      #ff6f00,
      #ff0000
    ); /* Màu gradient từ trái sang phải */
    background-size: 200% 200%;
    animation: gradientAnimation 3s ease infinite; /* 3s là thời gian của animation */
  }
  .gifs {
    position: absolute;
    top: 5px;
    right: 5px;
    cursor: pointer;
    color: green;
  }
  .themVaoGio {
    button {
      &.coHang {
        font-size: 14px;
        font-weight: 400;
        border-radius: 30px;
        margin: 10px 0 10px;
        width: 100%;

        background: linear-gradient(
          90deg,
          #ff0000,
          #ff6f00,
          #ff0000
        ); /* Màu gradient từ trái sang phải */
        background-size: 200% 200%;
        animation: gradientAnimation 6s ease infinite; /* 3s là thời gian của animation */
      }

      &.tamHet {
        font-size: 14px;
        font-weight: 400;
        border-radius: 30px;
        margin: 10px 0 10px;
        width: 100%;

        background-color: rgb(190, 190, 190);
        cursor: not-allowed;
      }
    }
  }
  .tangGiamSoLuong {
    margin: 10px 0 10px;
    padding: 0 10px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    i.giam {
      width: 22px;
      height: 22px;
      line-height: 21px;
      text-align: center;
      color: silver;
      background-color: white;
      border: 1px solid silver;
      border-radius: 50%;
      cursor: pointer;
    }
    i.tang {
      width: 23px;
      height: 23px;
      line-height: 23px;
      text-align: center;
      color: white;
      background-color: rgba(195, 195, 195, 0.6);
      border-radius: 50%;
      cursor: pointer;
    }
    p {
      height: 26px;
      line-height: 26px;
      text-align: center;
      width: 80px;
      background-color: rgb(255, 245, 245);
      color: red;
      border-radius: 20px;
    }
  }
}
@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

