.leftContent {
  width: 600px;
  table#formNhapHang {
    font-size: 14px;
    // width: 600px;
    img {
      width: 40px;
      height: 40px;
      border-radius: 10px;
    }
    .right {
      text-align: right;
    }
    tr {
      td {
        p {
          text-transform: capitalize;
          margin-bottom: 5px;
        }
        .flex {
          display: flex;
          gap: 20px;

          span {
            color: silver;
            font-size: 12px;
            display: block;
            width: 120px;
            text-transform: uppercase;
            margin-top: 3px;
          }
        }
        input {
          text-align: right;
          font-size: 14px;
        }
        input.giaNhap {
          text-align: right;
        }
        button {
          height: 22px;
          width: 22px;
          border-radius: 5px;
          margin: 0;
          i {
            font-size: 13px;
            line-height: 20px;
          }
        }
      }
      td.hinhAnh {
        text-align: center;
        width: 50px;
      }
      td.dvt {
        text-transform: capitalize;
        text-align: left;
        width: 60px;
      }
      td.donGia {
        width: 90px;
        text-align: right;
      }
      td.soLuong {
        width: 60px;
        text-align: right;
      }
    }
  }
  .taoPhieuNhap {
    width: 160px;
    padding: 0;
    margin: 0;
    font-size: 15px;
  }
  h4 {
    text-align: center;
    color: silver;
  }
}
