#thuongHieuSanPham {
  .container {
    .ant-tabs-ink-bar {
      display: none;
    }
    .ant-tabs-nav-operations {
      display: none;
    }
    .ant-tabs {
      width: 100%;
      .ant-tabs-nav {
        border-bottom: 1px solid rgb(220, 220, 220);

        .ant-tabs-nav-wrap-ping-left {
          &::before {
            display: none;
          }
        }

        .ant-tabs-nav-wrap-ping-right {
          &::after {
            display: none;
          }
        }

        .ant-tabs-nav-wrap {
          .ant-tabs-nav-list {
            // display: flex;
            // flex-wrap: wrap;
            display: grid;
            grid-template-columns: repeat(32, 1fr); /* Set four columns */
            grid-template-rows: repeat(3, 1fr); /* Set three rows */
            grid-gap: 5px; /* Adjust the gap between rows */
            padding: 5px;

            .ant-tabs-tab {
              margin: 1px;
              padding: 1px;
              width: 60px;
              height: 60px;
              justify-content: center;
              align-items: center;
              border-radius: 5px;
              .tabLabel {
                padding: 2px;
                border: 1px solid rgb(220, 220, 220);
                border-radius: 5px;
                display: flex;
                justify-content: center;
                align-items: center;
                img {
                  width: 56px;
                  height: 56px;
                  border-radius: 6px;
                }
              }
            }
          }
        }
      }
